 /* GLOBAL IMPORT */
 
 @import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');
 /* MAIN PART */
 
 * {
     box-sizing: border-box
 }
 
 body {
     font-family: 'Kumbh Sans', sans-serif;
     margin: 0;
     width: 375px;
     max-width: 90%;
     height: 100vh;
     font-size: 16px;
     overflow-x: hidden;
 }
 
 main {
     margin-top: 50px;
     margin-left: 70px;
     position: absolute;
 }
 
 #slide {
     margin: 30px;
 }
 
 #mainTitle {
     color: hsl(26, 100%, 55%);
     display: inline;
 }
 
 #modelName {
     font-weight: 700;
     font-size: 50px;
     margin-top: 0;
     padding: 0;
 }
 
 #productDescription {
     font-size: 16px;
     color: hsl(219, 9%, 45%)
 }
 
 #productPrice {
     display: flex;
 }
 
 #price {
     font-weight: 700;
     font-size: 30px;
     margin: 20px;
     margin-left: 0;
 }
 
 #discount {
     display: inline;
     padding: 5px;
     border-radius: 20%;
     font-weight: 700;
     font-size: 20px;
     color: hsl(26, 100%, 55%);
     background-color: hsl(25, 100%, 94%);
 }
 
 #oldPrice {
     font-weight: 700;
     font-size: 20px;
     color: hsl(220, 14%, 75%);
 }
 
 #product {
     float: left;
     border-radius: 5%;
     max-width: 100%;
     height: 35%;
     width: 500px;
 }
 
 .productThumb {
     height: 6em;
     width: 6em;
     margin: 8px;
     border-radius: 5%;
 }
 
 #thumbnail {
     clear: left;
     float: left;
     position: relative;
     margin-left: 20px;
 }
 
 #text {
     display: grid;
     box-sizing: border-box;
     margin: 0;
     padding: 50px;
 }
 
 body {
     width: 375px;
     max-width: 90%;
     height: 100vh;
     font-size: 16px;
 }
 /* -----------------------------------
Header style

-------------------------------------*/
 
 header {
     width: 100%;
     height: 15vh;
     display: flex;
     flex-direction: row;
 }
 
 .header-top {
     display: flex;
     flex-direction: row;
     margin-top: 1rem;
 }
 
 h1 {
     margin-left: 1rem;
 }
 
 #menu-bar {
     position: absolute;
     display: flex;
     flex-direction: column;
     list-style: none;
     background-color: white;
     width: 70%;
     height: 100%;
     transform: translateX(-100%);
     transition: 1s ease;
     z-index: 1;
 }
 
 #menu-bar.show {
     transform: translateX(0);
 }
 
 #menu-bar li {
     margin-top: 1rem;
     margin-left: 2rem;
 }
 
 #menu-bar li a {
     text-decoration: none;
     color: black;
     padding: 10px;
     margin: 1rem 0;
     font-size: 1.2rem;
     font-weight: bold;
 }
 
 #menu-bar li a:hover {
     border-bottom: 3px solid orange;
     color: black;
 }
 
 body.dark {
     background-color: gray;
 }
 
 #close-menu {
     margin-top: 1rem;
     border: none;
     background-color: transparent;
     position: relative;
     padding-right: 70%;
     font-size: 1.3rem;
     font-weight: bold;
     cursor: pointer;
 }
 
 #close-menu:hover {
     color: red;
 }
 
 #menu-btn {
     width: 25px;
     height: 25px;
     position: fixed;
     top: 4rem;
     cursor: pointer;
     left: 5%;
 }
 
 
 #account-icon {
     position: relative;
     cursor: pointer;
     width: 30px;
     height: 30px;
     top: -5.5rem;
     left: 47%;

    }
 

 
 .header-top {
     position: relative;
     left: 30%;
 }
 /* ---------------------------------
 Shopping icon and create progile 
 -------------------------------*/
 
 #articel-number {
     font-size: .7rem;
     border-radius: 50%;
     padding: 1px 6px;
     background-color: #FF7F18;
     position: relative;
     left: -1.1rem;
     top: -1.2rem;
     color: white;
 }
 
 #add-to-cart {
     position: relative;
     left: 10%;
     top: 2rem;
     width: 300px;
     height: 60px;
     border-radius: 10px;
     margin-bottom: 5rem;
     background-color: #FF7F18;
 }
 
 #add-to-cart span {
     margin-left: 1rem;
     font-size: 1.3rem;
     color: white;
     font-weight: bold;
     cursor: pointer;
 }
 
 #shop-basket {
     position: absolute;
     height: auto;
     width: 370px;
     background-image: url('./images/image-product-1.jpg');
     background-position: center;
     background-size: cover;
     padding: 10px;
     right: -3rem;
     border: 1px solid black;
     background-color: white;
     display: none;
     justify-content: center;
     align-items: center;
     z-index: 1;
 }
 
 #shop-basket.show {
     display: flex;
 }
 
 .conrtorls .calc {
     width: 300px;
     height: 60px;
     background-color: rgb(214, 211, 205);
     border-radius: 10px;
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding: 5px;
     position: relative;
     left: 2rem;
 }


 .shopping-sec{
     position: relative;
     top: -5rem;
     left: 45%;
 }
 /*----------------------------------------------------
Incress and decrese buttons
-------------------------------------------------------*/
 
 #decrese,
 #incress {
     border: none;
     background-color: transparent;
     font-size: 1.7rem;
     cursor: pointer;
 }
 
 #atc-in-basket {
     position: relative;
     width: 99%;
     height: 99%;
     background-color: white;
     border-radius: 10px;
 }
 
 #basket-title {
     padding-left: 15px;
     width: 100%;
 }
 
 hr {
     opacity: 0.1;
 }
 
 input {
     border: none;
     background-color: transparent;
     position: relative;
     left: 14px;
     font-size: 1.3rem;
     width: 60px;
 }
 
 #basket-body {
     display: flex;
     flex-direction: column;
     align-items: center;
     text-align: center;
 }
 
 .empty {
     padding-top: 1rem;
     font-weight: bold;
     padding: 20px 0;
     opacity: 0.4;
 }
 
 .hide {
     display: none;
 }
 
 .prd-img {
     width: 70px;
     height: 70px;
     position: relative;
     top: 1.5rem;
 }
 
 .firstDiv {
     display: grid;
     grid-template-columns: repeat(3, auto);
 }
 
 .namePrice {
     width: 98%;
 }
 
 .priceAndTotal {
     display: flex;
     position: relative;
     left: 2rem;
     top: -2rem;
 }
 
 .delete-icon {
     position: relative;
     top: 2rem;
     left: 10px;
     width: 20px;
     height: 20px;
     cursor: pointer;
 }
 
 .check-out-btn {
     width: 300px;
     height: 60px;
     border-radius: 10px;
     margin-bottom: 2rem;
     background-color: #FF7F18;
     cursor: pointer;
     position: relative;
     font-size: 1.2rem;
     color: white;
     font-weight: bold;
     opacity: 0.9;
     opacity: 0.9;
 }
 
 .total {
     font-weight: bold;
 }
 /*---------------------------- 
if Qte is egal to null
------------------------*/
 
 .qte-null {
     width: 350px;
     height: 200px;
     background-color: white;
     color: black;
     font-size: 1.5rem;
     text-align: center;
     display: grid;
     place-items: center;
     padding: 5px;
     position: absolute;
     left: 1%;
     top: 60%;
     box-shadow: 0 0 10px 5px;
     border-radius: 5px;
     display: none;
 }
 
 .qte-null.on {
     display: grid;
 }
 
 #ok:hover {
     color: black;
     cursor: pointer;
     padding: 5px;
     transform: scale(1.3);
 }